<template>
  <div class="popup popup1 task-popup" id="make_order" v-show="showStatus">
    <div class="pop-content">
      <p class="popup-title">
        选派工单
      </p>
      <div class="popup-con">
        <ul>
          <!-- <li :class="{'warning': $v.task.receiver.$dirty && $v.task.receiver.$invalid}"> -->
          <li>
            <span>现场勘验人</span>
            <input type="text" placeholder="请输入勘验人名称" v-model="task.receiver"/>
            <!-- <small v-show="$v.task.receiver.$dirty && $v.task.receiver.$invalid">*现场勘验人名称错误</small> -->
          </li>
          <li>
          <!-- <li :class="{'warning': $v.task.phone.$dirty && $v.task.phone.$invalid}"> -->
            <span>勘验人电话</span>
            <input type="text" placeholder="请输入勘验人联系电话" v-model="task.phone"/>
            <!-- <input type="text" placeholder="请输入勘验人联系电话" v-model="task.phone" @input="$v.task.phone.$touch"/> -->
            <!-- <small v-show="$v.task.phone.$dirty && $v.task.phone.$invalid">*请输入勘验人联系电话</small> -->
          </li>
          <li>
            <span>设备MAC</span>
            <p>{{task.mac}}</p>
          </li>
          <li>
            <span>告警时间</span>
            <p>{{task.time}}</p>
          </li>
          <li>
            <span>安装位置</span>
            <p>{{task.address}}</p>
          </li>
          <li>
            <button class="btn btn-middle cancel" @click="cancel">取消</button>
            <button class="btn btn-middle yes" @click="submit">确定</button>
          </li>
        </ul>
      </div>
      <!--关闭按钮-->
      <span class="layui-layer-setwin">
				<a class="close-popup layui-layer-close layui-layer-close1" href="javascript:;" @click="cancel">
          <span class="iconfont iconguanbi close-iconfont"></span>
				</a>
			</span>
    </div>
  </div>
</template>
<script>
import { withParams } from 'vuelidate/lib/validators/common'
import { required } from 'vuelidate/lib/validators'
import msgUtil from '@/utils/msgUtil'
import { addTask } from '@/api/monitor'

export default {
  data() {
    return {
      showStatus: false,
      task: {
        phone: '',
        receiver: '',
        alarm_id: ''
      }
    }
  },
  validations: {
    task: {
      alarm_id: {
        required
      },
      receiver: {
        required
      },
      phone: {
        required,
        format: withParams({ type: 'format' }, val => {
          return val && /^1[3-9]\d{9}$/.test(val)
        })
      }
    }
  },
  methods: {
    show(info) {
      // 恢复验证
      // this.$v.task.phone.$reset()
      // this.$v.task.receiver.$reset()
      this.task = {
        alarm_id: info.id ? info.id + '' : undefined,
        phone: '',
        receiver: '',
        mac: info.mac,
        time: info.createTime,
        address: info.address
      }
      this.showStatus = true
      // this.$el.style.zIndex = Date.now() % 100000000
    },
    async submit() {
      // 触发验证
      // this.$v.task.phone.$touch()
      // this.$v.task.receiver.$touch()
      // if (!this.$v.task.$invalid) {
      try {
        await addTask(this.task)
        msgUtil.success('选派工单成功')
        if (this.$route.path === '/index/monitor/monitor-center') {
          this.$store.dispatch('addTask', this.task)
        }
        this.$emit('send-success')
      } catch (error) {
        msgUtil.error('选派工单失败', msgUtil.findErrorMsg(error))
      }
    },
    cancel() {
      this.showStatus = false
    }
  }
}
</script>
